const ToolsChip = ({ tool }: { tool: string }) => (
    <Chip
        label={tool}
        color={"primary"}
        size={"small"}
        sx={{
            my: .5
        }}
    />
)
interface PortfolioSectionProps {

}

const PortfolioSection: FunctionComponent<PortfolioSectionProps> = () => {
    const theme = useTheme();
    const newTheme = createTheme({
        ...theme,
        palette: {
            mode: "dark"
        }
    });
    return (
        <ThemeProvider
            theme={newTheme}
        >
            <Box
                sx={{
                    bgcolor: blueGrey[900],
                    py: 15
                }}
            >
                <Container>
                    {
                        portfolios.map(portfolio => (
                            <>
                                <SinglePortfolio
                                    portfolio={portfolio}
                                />
                                <Divider sx={{ my: 5 }} />
                            </>
                        ))
                    }
                    <Box
                        sx={{
                            pb: 5
                        }}
                    >
                        <Chip
                            icon={<Favorite />}
                            label={"other projects"}
                            size={"small"}
                            sx={{
                                mb: 1
                            }}
                        />
                        <Typography
                            variant={"h2"}
                            component={"h2"}
                            color={"text.primary"}
                            fontSize={35}
                            fontWeight={500}
                            sx={{
                                mb: 2
                            }}
                        >
                            A Glimpse of My Diverse Skill Set: Other Small Showcase Projects
                        </Typography>
                        <Stack
                            direction={"row"}
                            flexWrap={"wrap"}
                        >
                            {
                                smallPortfolios.map(prtf => (
                                    <Box
                                        sx={{
                                            width: {
                                                xs: "100%",
                                                tablet: "50%",
                                                // laptop: "50%",
                                            },
                                            p: 1,
                                            boxSizing: "border-box"
                                        }}
                                    >
                                        <SmallPortfolio
                                            portfolio={prtf}
                                        />
                                    </Box>
                                ))
                            }
                        </Stack>
                    </Box>
                </Container>
            </Box>
        </ThemeProvider>
    );
}

export default PortfolioSection;

